@media print {
  .hide-print {
    display: none;
  }
  .spinner-container {
    display: none;
  }
  @page {
    size: A4;
    margin: 0;
  }
  body {
    font-size: 9.5pt;
    font-family: 'Open Sans', sans-serif;
  }
  h1, .h1,
  h2, .h2,
  h3, .h3,
  h4, .h4,
  h5, .h5,
  h6, .h6 {
    font-family: 'Montserrat', sans-serif;
    page-break-after: avoid;
  }
  h1, .h1 {
    font-size: 36px;
    margin-top: 0.5555555556em;
    margin-bottom: 0.2777777778em;
  }
  h2, .h2 {
    font-size: 30px;
    margin-top: 0.6666666667em;
    margin-bottom: 0.3333333333em;
  }
  h3, .h3 {
    font-size: 24px;
    margin-top: 0.8333333333em;
    margin-bottom: 0.4166666667em;
  }
  h4, .h4 {
    font-size: 18px;
    margin-top: 0.5555555556em;
    margin-bottom: 0.5555555556em;
  }
  h5, .h5 {
    font-size: 14px;
    margin-top: 0.7142857143em;
    margin-bottom: 0.7142857143em;
  }
  h6, .h6 {
    font-size: 12px;
    margin-top: 0.8333333333em;
    margin-bottom: 0.8333333333em;
  }
  .subtitulo {
    font-size: 1.1428571429em;
    text-transform: uppercase;
  }
  a[href]:after {
    display: none;
  }
  .nav-listado,
  .acceso-usuario-container,
  .pie-pagina {
    display: none;
  }
  .hide-svg {
    visibility: hidden;
    width: 0;
    height: 0;
    position: absolute;
    margin-top: -999em;
  }
  .container-general-flex {
    display: block;
    min-height: 0;
  }
  .container {
    padding: 1cm 1cm 0.5cm;
  }
  .header-acceso {
    padding: 0;
    margin-top: 0.5cm;
    display: block;
  }
  .header-acceso .marca-container {
    margin: 0 auto;
    text-align: center;
  }
  .header-acceso .marca-container a {
    display: block;
  }
  .header-acceso .logo-mpt {
    width: 165px;
    fill: #2C72B4;
  }
  .egresado-individual {
    margin-top: 1.5cm;
  }
  .egresado-individual .fa-li::before,
  .egresado-individual .texto-azul {
    color: #2C72B4 !important;
  }
  .egresado-individual .egresado-info-principal {
    display: block;
    border-bottom: 2px solid #2C72B4;
    position: relative;
    width: 100%;
  }
  .egresado-individual .egresado-info-principal:before, .egresado-individual .egresado-info-principal:after {
    content: " ";
    display: table;
  }
  .egresado-individual .egresado-info-principal:after {
    clear: both;
  }
  .egresado-individual .egresado-info-principal .contenedor-foto {
    position: absolute;
    width: 20%;
    left: 0;
    bottom: 10px;
  }
  .egresado-individual .egresado-info-principal .info-egresado {
    position: absolute;
    width: 65%;
    left: 20%;
    bottom: 10px;
  }
  .egresado-individual .egresado-info-principal .promedio {
    position: absolute;
    width: 15%;
    right: 0;
    bottom: 0;
  }
  .egresado-individual .contenedor-foto {
    width: auto;
    margin: 0 0.5cm 0.5cm 0.25cm;
  }
  .egresado-individual .foto-egresado {
    width: 2cm;
    height: 2cm;
    margin: 0;
    border-radius: 0;
  }
  .egresado-individual .foto-egresado.sin-foto {
    display: none;
  }
  .egresado-individual .nombre-egresado {
    font-size: 1.8em;
    margin: 0 0 0.25em;
    font-weight: 700;
  }
  .egresado-individual .datos-egresado span {
    margin: 0.5em 1em 0.5em 0;
    line-height: 1.75;
    white-space: nowrap;
  }
  .egresado-individual .promedio {
    padding: 1.5em 0em 0.5em;
    text-align: right;
  }
  .egresado-individual .promedio .promedio-titulo {
    text-transform: uppercase;
    display: block;
    line-height: 1.2;
  }
  .egresado-individual .promedio .promedio-valor {
    color: #2C72B4 !important;
    font-size: 1.7142857143em;
    font-weight: 700;
  }
  .egresado-individual .fila-flex {
    display: block !important;
    position: relative;
  }
  .egresado-individual .fila-flex:before, .egresado-individual .fila-flex:after {
    content: " ";
    display: table;
  }
  .egresado-individual .fila-flex:after {
    clear: both;
  }
  .egresado-individual .info-contactos {
    width: 6cm;
    margin-right: 0.5cm;
  }
  .egresado-individual .info-contactos section {
    padding: 1em;
    margin-bottom: 1em;
    border-top: 2px solid #2C72B4;
  }
  .egresado-individual .info-contactos section.contacto-egresado {
    border-top: none;
  }
  .egresado-individual .info-contactos section.contacto-docente li {
    padding: 0.1em 0 0.2em;
  }
  .egresado-individual .info-contactos .lista-contacto {
    margin-top: 1em;
  }
  .egresado-individual .info-contactos .lista-contacto li {
    padding: 0.5em 0 0.5em 0.5em;
  }
  .egresado-individual .info-contactos .lista-contacto .fa-li {
    background-color: #2C72B4;
    color: #fff;
    border-radius: 100%;
    width: 2em;
    height: 2em;
    font-size: 10pt;
    line-height: 2em;
    padding: 0;
  }
  .egresado-individual .info-detalle {
    width: 10cm;
    padding: 1em 0.5em 0;
    position: absolute;
    left: 6.5cm;
    top: 0;
  }
  .egresado-individual .info-detalle section {
    margin-bottom: 0.5em;
    padding-bottom: 0.5em;
  }
  .egresado-individual .info-detalle section.carta-presentacion {
    page-break-inside: avoid;
    padding: 0;
  }
  .egresado-individual .info-detalle .lista-actitudes li {
    padding: 0.5em 0;
    margin-right: 2.5em;
  }
  .egresado-individual .info-detalle .lista-actitudes .fa-li {
    left: -1.75em;
    width: 1.8em;
    top: 0.75em;
  }
}
